// A mock function to mimic making an async request for data
import { configUrl } from "../../app/contants";
export function addToCartProduct(productData) {
  return new Promise(async (resolve) => {
    const response = await fetch(`${configUrl.url}/cart`, {
      method: "POSt",
      body: JSON.stringify(productData),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    resolve({ data });
  });
}

export function fetchAllItemByUserId(userId) {
  return new Promise(async (resolve) => {
    const response = await fetch(`${configUrl.url}/cart?user=${userId}`);
    const data = await response.json();
    resolve({ data });
  });
}

export function updateCart(update) {
  return new Promise(async (resolve) => {
    const response = await fetch(`${configUrl.url}/cart/${update.id}`, {
      method: "PATCH",
      body: JSON.stringify(update),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    resolve({ data });
  });
}

export function deleteItemInCart(itemId) {
  return new Promise(async (resolve) => {
    const response = await fetch(`${configUrl.url}/cart/${itemId}`, {
      method: "Delete",
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    resolve({ data: { id: itemId } });
  });
}

export async function resetCart(userId) {
  return new Promise(async (resolve) => {
    const response = await fetchAllItemByUserId(userId);
    const items = response.data;
    for (let item of items) {
      await deleteItemInCart(item.id);
    }
    resolve({status:'success'});
  });
}
