import React, { useEffect, useState } from "react";
import "./App.css";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Home from "./pages/Home";
import LoginPage from "./pages/LoginPage";
import SignupPage from "./pages/SignupPage";
import { createBrowserRouter, RouterProvider} from "react-router-dom";
import CartPage from "./pages/CartPage";
import Checkout from "./pages/Checkout";
import ProductDetailsPage from "./pages/ProductDetailsPage";
import Protected from "./features/auth/components/Protected";
import { useDispatch, useSelector } from "react-redux";
import { checkAuthAsync, selectError, selectIsRedirect, selectLoggedInUser, selectUserChecked } from "./features/auth/authSlice";
import { fetchAllItemByUserIdAsync } from "./features/cart/cartSlice";
import PageNotFound from "./pages/404";
import OrderSuccess from "./pages/OrderSuccess";
import UserOrdersPage from "./pages/UserOrdersPage";
import UserAccountPage from "./pages/UserAccountPage";
import { fetchLoggedInUserAsync } from "./features/user/userSlice";
import Logout from "./features/auth/components/Logout";
import ForgotPasswordPage from "./pages/ForgotPasswordPage";
import ProtectedAdmin from "./features/auth/components/ProtectedAdmin";
import AdminHome from "./pages/AdminHome";
import AdminProductDetailsPage from "./pages/AdminProductDetailsPage";
import AdminProductFormPage from "./pages/AdminProductFormPage";
import AdminOrderPage from "./pages/AdminOrderPage";
import DashboardPage from "./pages/DashboardPage";
import { transitions, positions, Provider as AlertProvider } from 'react-alert'
import AlertTemplate from 'react-alert-template-basic'
import NewArrivalsPage from "./pages/NewArrivalsPage";
import AdminNewArrivalsPage from "./pages/AdminNewArrivalsPage";
import Loader from "./features/common/Loader";
import PaymentCheckoutPage from "./pages/PaymentCheckoutPage";
import ReportPage from "./pages/ReportPage";
import ResetPasswordPage from "./pages/ResetPasswordPage";
const router = createBrowserRouter([
  {
    path: "/",
    element: (
      <Protected>
        <DashboardPage />
      </Protected>
    ),
  },
  {
    path: "/product-list",
    element: (
      <Protected>
        <Home />
      </Protected>
    ),
  },
  {
    path: "/admin/product-list",
    element: (
      <ProtectedAdmin>
        <AdminHome />
      </ProtectedAdmin>
    ),
  },
  {
    path: "/login",
    element: <LoginPage />,
  },
  {
    path: "/reset-password",
    element: <ResetPasswordPage />,
  },
  
  {
    path: "/dashboard",
    element: (
      <Protected>
        <DashboardPage />
      </Protected>
    ),
  },
  {
    path: "/signup",
    element: <SignupPage />,
  },
  {
    path: "/cart",
    element: (
      <Protected>
        <CartPage />
      </Protected>
    ),
  },
  {
    path: "/checkout",
    element: (
      <Protected>
        <Checkout />
      </Protected>
    ),
  },
  {
    path: "/orders",
    element: (
      <Protected>
        <UserOrdersPage />
      </Protected>
    ),
  },
  {
    path: "/account",
    element: (
      <Protected>
        <UserAccountPage />
      </Protected>
    ),
  },
  {
    path: "/new-arrivals",
    element: (
      <Protected>
        <NewArrivalsPage />
      </Protected>
    ),
  },
  {
    path: "/reports",
    element: (
      <Protected>
        <ReportPage />
      </Protected>
    ),
  },
  {
    path: "/order-success/:id",
    element: (
      <Protected>
        <OrderSuccess />
      </Protected>
    ),
  },
  {
    path: "/logout",
    element: <Logout />,
  },
  {
    path: "/forgot-password",
    element: <ForgotPasswordPage />,
  },
  {
    path: "/product-details/:id",
    element: (
      <Protected>
        <ProductDetailsPage />
      </Protected>
    ),
  },
  {
    path: "/stripe-checkout/:id",
    element: (
      <Protected>
        <PaymentCheckoutPage />
      </Protected>
    ),
  },
  {
    path: "/admin/product-details/:id",
    element: (
      <ProtectedAdmin>
        <AdminProductDetailsPage />
      </ProtectedAdmin>
    ),
  },
  {
    path: "/admin/new-product-form",
    element: (
      <ProtectedAdmin>
        <AdminProductFormPage />
      </ProtectedAdmin>
    ),
  },
  
  {
    path: "/admin/orders",
    element: (
      <ProtectedAdmin>
        <AdminOrderPage />
      </ProtectedAdmin>
    ),
  },
  {
    path: "/admin/new-product-form/edit/:id",
    element: (
      <ProtectedAdmin>
        <AdminProductFormPage />
      </ProtectedAdmin>
    ),
  },
  {
    path: "*",
    element: <PageNotFound />,
  },
  {
    path: "/admin/new-arrivals",
    element: (
      <ProtectedAdmin>
        <AdminNewArrivalsPage />
      </ProtectedAdmin>
    ),
  },
]);
// optional configuration
const options = {
  // you can also just use 'bottom center'
  position: positions.BOTTOM_RIGHT,
  timeout: 5000,
  offset: '30px',
  // you can also just use 'scale'
  transition: transitions.SCALE
}

function App() {
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);
  const userChecked = useSelector(selectUserChecked);
  const selectIsRedirectURL = useSelector(selectIsRedirect);

  useEffect(()=>{
  dispatch(checkAuthAsync());
  
  },[dispatch,userChecked,selectIsRedirectURL])
  useEffect(() => {
    if (user) {
      dispatch(fetchAllItemByUserIdAsync(user.id));
      dispatch(fetchLoggedInUserAsync(user.id));
    }
  }, [dispatch, user]);
  return (
    <AlertProvider template={AlertTemplate} {...options}>
    <div className="App object-cover relative top-0 filter contrast-125">
       <><RouterProvider router={router} />
      <ToastContainer />
      </> 
    </div>
  </AlertProvider>
  );
}

export default App;
