import React from 'react';
import { PRODUCT_PER_PAGE } from '../../app/contants';
import {
    ChevronLeftIcon,
    ChevronRightIcon,
  } from "@heroicons/react/20/solid";
const Pagination = ({page,setPage,handlePage,totalItem}) => {
    const totalPage =Math.ceil(totalItem/PRODUCT_PER_PAGE);   
  return (
    <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
            <div className="flex flex-1 justify-between sm:hidden">
              <div  onClick={e=>handlePage(page >1 ?page-1 : page)}
                className="relative cursor-pointer inline-flex  items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
              >
                Previous
              </div>
              <div onClick={e=>handlePage(page < totalPage ?page+1 : page)}
                className="relative cursor-pointer ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
              >
                Next
              </div>
            </div>
            <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
              <div>
                <p className="text-sm text-indigo-700">
                  Showing <span className="font-medium">{(page-1)*PRODUCT_PER_PAGE+1}</span> to{" "}
                  <span className="font-medium">{page*PRODUCT_PER_PAGE> totalItem ? totalItem :page*PRODUCT_PER_PAGE }</span> of{" "}
                  <span className="font-medium">{totalItem}</span> results
                </p>
              </div>
              <div>
                <nav
                  className="isolate inline-flex -space-x-px rounded-md shadow-sm"
                  aria-label="Pagination"
                >
                  <div onClick={e=>handlePage(page >1 ?page-1 : page)}
                    className="relative cursor-pointer inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                  >
                    <span className="sr-only">Previous</span>
                    <ChevronLeftIcon className="h-5 w-5 text-indigo-600 hover:bg-indigo-600 hover:text-white" aria-hidden="true" />
                  </div>
                  {/* Current: "z-10 bg-indigo-600 text-white focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600", Default: "text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:outline-offset-0" */}
                  
                  {Array.from({length:totalPage}).map((el,index)=>(
                    <div
                    key={index}
                    onClick={e=>handlePage(index+1)}
                    aria-current="page"
                    className={`relative cursor-pointer z-10 inline-flex items-center ${index+1 === page ? "bg-indigo-600 text-white"  :" text-indigo-500 border  border-gray-300 hover:bg-indigo-600 hover:text-white" } px-4 py-2 text-sm font-semibold  focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600`}
                  >
                    { index+1}
                  </div>
                  ))
                
                
                
                }
                  
                  
                  <div onClick={e=>handlePage(page < totalPage ?page+1 : page)}
                    className="relative cursor-pointer inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                  >
                    <span className="sr-only">Next</span>
                    <ChevronRightIcon className="h-5 w-5 text-indigo-600 hover:bg-indigo-600 hover:text-white" aria-hidden="true" />
                  </div>
                </nav>
              </div>
            </div>
          </div>
  )
}

export default Pagination;

