import React,{useState,useEffect} from 'react'
import { Link,useNavigate,useSearchParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { useForm } from "react-hook-form";
import Loader from '../../common/Loader';
import { resetPasswordAsync,selectPasswordReset,selectAuthStatus } from '../authSlice';
import { GlobeAltIcon } from '@heroicons/react/20/solid';
import {EyeSlashIcon,EyeIcon} from "@heroicons/react/24/outline"
const Resetpassword = () => {
    const [passwordView,setPasswordView]=useState(false)
    const { register, handleSubmit, reset,watch, formState: { errors } } = useForm();
    const dispatch = useDispatch();
    const [searchParams, setSearchParams] = useSearchParams();
    const email=searchParams.get('email');
    const token =searchParams.get('token');
    const navigate =useNavigate();
    const isResetPassword =useSelector(selectPasswordReset);
    const logInStatus =useSelector(selectAuthStatus)
    const handleResetPassword =(data)=>{

        dispatch(resetPasswordAsync({email: email, token: token, password:data.password}));
        reset();
    }
    useEffect(()=>{
        if(isResetPassword)
        navigate("/login",{replace:true});
    },[isResetPassword,navigate])
    const handleShowHidePassword =(e)=>{
        setPasswordView(!passwordView);
      }
  return (
    <>
    {logInStatus=== "loading" ? <Loader /> : null}
      { (email && token ) ? <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
        <div className="flex flex-col justify-center items-center sm:mx-auto sm:w-full sm:max-w-sm">
        <GlobeAltIcon className='h-30 w-32 text-indigo-600'/>
          <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900">
            Rest password for your account
          </h2>
        </div>

        <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
          <form noValidate className="space-y-6" onSubmit={handleSubmit(handleResetPassword)} >
            <div>
              <label htmlFor="password" className="block text-sm font-medium leading-6 text-gray-900">
                New Password<span className="text-red-500">*</span>
              </label>
              <div className="mt-2 relative">
                <input
                  id="password"
                  {...register("password", { required: "password are required" ,
                  pattern: {
                        value: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/gm,
                        message: `- at least 8 characters\n\n
                        - must contain at least 1 uppercase letter, 1 lowercase letter, and 1 number \n\n
                        - Can contain special characters`,
                      },
                  })}
                  type={ passwordView ? "text":`password`}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                <div className="absolute inset-y-0 right-5 flex items-center justify-center">
                    <label htmlFor="password" className="sr-only">Show Password</label>
                     {passwordView ?<EyeSlashIcon className="h-6 w-6 text-indigo-600 cursor-pointer" onClick={handleShowHidePassword} /> :<EyeIcon className="h-6 w-6 text-indigo-600 cursor-pointer" onClick={handleShowHidePassword}/>}
                  </div>
                
              </div>
              {errors.password &&<p className='text-red-500'>{errors.password.message}</p>}
            </div>
            <div>
              <label htmlFor="confirmPassword" className="block text-sm font-medium leading-6 text-gray-900">
                Confirm Password<span className="text-red-500">*</span>
              </label>
              <div className="mt-2">
                <input
                  id="confirmPassword"
                  {...register("confirmPassword", { required: "Confirm Password are required" ,
                  validate:(value,formValues)=> value ===formValues.password || 'password did not match',
                  
                  })}
                  type={ passwordView ? "text":`password`}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                
              </div>
              {errors.confirmPassword &&<p className='text-red-500'>{errors.confirmPassword.message}</p>}
            </div>
        <div>
              <button
                type="submit"
                className="flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Rest Password
              </button>
            </div>
          </form>

          <p className="mt-10 text-center text-sm text-gray-500">
            Send me back to {' '}
            <Link to="/login" className="font-semibold leading-6 text-indigo-600 hover:text-indigo-500">
              Login
            </Link>
          </p>
        </div>
      </div> :<>
        Token Expired
      </>}
    </>
  )
}

export default Resetpassword;
