import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { createUser,validUser,signOutUser,checkAuth,resetPasswordRequest,resetPassword} from './authAPI';
import { updateUser } from '../user/userAPI';
import { notify } from '../common/notify';

const initialState = {
  loggedInUser: null,
  status: 'idle',
  error:null,
  userChecked:false,
  isMailSent:false,
  isPasswordReset:false,
  isRedirect:false,
};

export const createUserAsync = createAsyncThunk(
  'user/createUser',
  async (userData,{rejectWithValue}) => {
    try{
      const response = await createUser(userData);
      // The value we return becomes the `fulfilled` action payload
      return response.data;
    }
    catch(er){
          return rejectWithValue(er);
    }
    
  }
);
export const resetPasswordRequestAsync = createAsyncThunk(
  'user/resetPasswordRequest',
  async (userData,{rejectWithValue}) => {
    try{
    const response = await resetPasswordRequest(userData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
    }
    catch(er){
      return rejectWithValue(er);
}
  }
);
export const resetPasswordAsync = createAsyncThunk(
  'user/resetPassword',
  async (userData,{rejectWithValue}) => {
    try{
    const response = await resetPassword(userData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
    }catch(er){
      return rejectWithValue(er);
}
  }
);

export const checkAuthAsync =createAsyncThunk(
  'user/checkAuth',
  async (demo="",{rejectWithValue}) => {
    try{
      const response = await checkAuth();
      // The value we return becomes the `fulfilled` action payload
      return response.data;
    }
    catch(error){
      // return error;
        return rejectWithValue(error);
    }
  }
);

export const signOutUserAsync = createAsyncThunk(
  'user/signOutUser',
  async () => {
    const response = await signOutUser();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const updateUserAsync = createAsyncThunk(
  'user/updateUser',
  async (update) => {
    const response = await updateUser(update);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const validUserAsync = createAsyncThunk(
  'user/validUser',
  async (userData,{rejectWithValue}) => {
    try{
      const response = await validUser(userData);
      // The value we return becomes the `fulfilled` action payload
      return response.data;
    }
    catch(error){
      console.log(error)
        return rejectWithValue(error);
    }

  }
);


export const authSlice = createSlice({
  name: 'user',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.error=null;
        state.isRedirect=false;
        state.loggedInUser = action.payload;
        // notify.success(action.payload.fullName + ' Created account Successfully');
      })
      .addCase(createUserAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.loggedInUser = null;
        state.isRedirect=false;
        state.error=action.error;
        notify.error(action.error.message);
      })
      .addCase(validUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(validUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedInUser = action.payload;
        state.error=null;
        state.isRedirect=false;
        notify.success(state.loggedInUser.fullName+" Login Success");
      })
      .addCase(validUserAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error =action?.error ||  action?.payload ;
        state.isRedirect=false;
        notify.error( `Invalid Email And Password`);
      })
      .addCase(updateUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.isRedirect=false;
        state.loggedInUser = action.payload;
      })
      .addCase(signOutUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(signOutUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedInUser = null;
        state.isRedirect=true;
        notify.success("User signed out");
      })
      .addCase(checkAuthAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(checkAuthAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.loggedInUser = action.payload;
        state.error=null;
        state.userChecked=true;
        state.isRedirect=false;
        notify.success(state.loggedInUser.fullName+" Login Success");
      })
      .addCase(checkAuthAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error = null;
        state.userChecked=true;
        state.isRedirect =true;
        // notify.error(action.payload.message);
      })
      .addCase(resetPasswordRequestAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(resetPasswordRequestAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.error=null;
        state.isMailSent=true;
        // notify.success(state.loggedInUser.fullName+" Login Success");
      })
      .addCase(resetPasswordRequestAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error = action.error;
        state.isMailSent=false;
        notify.error(action.error.message);
      })
      .addCase(resetPasswordAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(resetPasswordAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.error=null;
        state.isMailSent=true;
        state.isPasswordReset=true;
        state.isRedirect=false;
        notify.success(action.payload.message);
      })
      .addCase(resetPasswordAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error = action.error;
        state.isMailSent=false;
        state.isPasswordReset=false;
        state.isRedirect=false;
        notify.error(action?.error?.message || action?.payload?.message || action?.payload?.err);
      })
  }
});

export const { increment } = authSlice.actions;

export const selectLoggedInUser = (state) => state.auth.loggedInUser;

export const selectError = (state) => state.auth.error;
export const selectAuthStatus = (state) => state.auth.status;
export const selectUserChecked =(state)=> state.auth.userChecked;
export const selectMailSent =(state)=> state.auth.isMailSent;
export const selectPasswordReset =(state)=>state.auth.isPasswordReset;
export const selectIsRedirect =(state)=> state.auth.isRedirect;
export default authSlice.reducer;
