export const PRODUCT_PER_PAGE=10;
export const discountPrice =(item)=>{
    return Math.round(item.price *(1- item.discountPercentage/100),2);
}
const productionUrl={
    url:"http://localhost:5002/api",
    frontendUrl:"http://localhost:5002/app"
}
const developmentUrl={
    url:"http://localhost:5002/api",
    frontendUrl:"http://localhost:3000/app"
}
export const configUrl=process.env.NODE_ENV !== "production" ? developmentUrl: productionUrl;