import React, { useState,Fragment, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  addToCartProductAsync, selectAddToCartProduct,updateCartAsync,deleteItemInCartAsync, selectCartStatus, selectCartLoaded
} from './cartSlice';

import { Dialog, Transition } from '@headlessui/react'
import { XMarkIcon } from '@heroicons/react/24/outline'
import { Link ,Navigate} from 'react-router-dom';
import { discountPrice } from '../../app/contants';
import Loader from '../common/Loader';
import Modal from '../common/Modal';


export default function Cart() {
  const dispatch = useDispatch();
  const [openModal,setOpenModal] = useState(null);
const items=useSelector(selectAddToCartProduct);
const totalAmount= items.reduce((amount, item) => discountPrice(item.product)* item.quantity+amount,0);
const totalItems= items.reduce((amount, item) => item.quantity+amount,0);
const status = useSelector(selectCartStatus);
const cartLoaded = useSelector(selectCartLoaded);
const handleSelectQuantity =(e,item)=>{
  dispatch(updateCartAsync({id:item.id,quantity :  +e.target.value}));
}
// const handleRemoveItem =(e,item)=>{
//   setOpenModal(!openModal); 
// }
const handleModalRemove=(e,item)=>{
  dispatch(deleteItemInCartAsync(item.id))
  setOpenModal(-1);
}


  return (
    <>{!items.length && cartLoaded && <Navigate to={"/"} replace={true} />}
    {status === "loading" ? <Loader /> : null}
    <div className='mx-auto mt-12 max-w-7xl px-4 sm:px-6 lg:px-8 bg-white'>
    
      <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
      <h1 className="text-4xl my-5 font-bold tracking-tight text-indigo-800">Cart</h1>
                        <div className="flow-root">
                          <ul className="-my-6 divide-y divide-gray-200">
                            {items.map((product,index) => (
                              <li key={index} className="flex py-6">
                                <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                  <img
                                    src={product.product.thumbnail}
                                    alt={product.product.title}
                                    className="h-full w-full object-cover object-center"
                                  />
                                </div>

                                <div className="ml-4 flex flex-1 flex-col">
                                  <div>
                                    <div className="flex justify-between text-base font-medium text-gray-900">
                                      <h3 className="text-indigo-600">
                                        <a href={product.product.href}>{product.product.title}</a>
                                      </h3>
                                      <p className="ml-4">${discountPrice(product.product)}</p>
                                    </div>
                                    <p className="mt-1 text-sm text-indigo-400">{product.product.brand}</p>
                                  </div>
                                  <div className="flex flex-1 items-end justify-between text-sm">
                                    <div className="text-gray-500"><label htmlFor="quantity" className="inline mr-5 text-sm font-medium leading-6 text-gray-900">
                  Qty
                </label>
                <select onChange={(e)=>handleSelectQuantity(e,product)} value={product.quantity}>
                <option value={"1"}>1</option>
                <option value={"2"}>2</option>
                <option value={"3"}>3</option>
                <option value={"4"}>4</option>
                <option value={"5"}>5</option>
                <option value={"6"}>6</option>
                
                </select>
                </div>

                                    <div className="flex">
                                      <button
                                       onClick={(e)=>{
  
                                        setOpenModal(product.id)}}
                                        type="button"
                                        className="font-medium text-indigo-600 hover:text-indigo-500"
                                      >
                                        Remove
                                      </button>
                                    </div>
                                  </div>
                                </div>
                                {/* {Modal worked Started} */}
                                  <Modal 
                                    title={"Delete "+ product.product.title + " from Cart"}
                                    message={"Are you sure you want to delete this item from Cart?"}
                                    dangerOption={"Delete"}
                                    cancelOption={"Cancel"}
                                    showModal={openModal === product.id}
                                    cancelModalAction={e=> setOpenModal(-1)}
                                    handleAction={(e)=>handleModalRemove(e,product)}
                                  />
                    {/* Modal worked ended */}
                              </li>
                            ))}
                          </ul>
                        </div>
                      </div>
                    

                    <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                      <div className="flex justify-between text-base my-2 font-medium text-indigo-800">
                        <p>Subtotal</p>
                        <p>$ {totalAmount}</p>
                      </div>
                      <div className="flex justify-between my-2 text-base font-medium text-indigo-800">
                        <p>Total item in Cart</p>
                        <p>${totalItems} items</p>
                      </div>
                      <p className="mt-0.5 text-sm text-gray-500">Shipping and taxes calculated at checkout.</p>
                      <div className="mt-6">
                        <Link to={"/checkout"}
                          className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                        >
                          Checkout
                        </Link>
                      </div>
                      <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
                        <p>
                          or{"  "}
                          <Link to={"/product-list"}>
                          <button
                            type="button"
                            className="font-medium text-indigo-600 hover:text-indigo-500"
            
                          >
                            {" "}Continue Shopping
                            <span aria-hidden="true"> &rarr;</span>
                          </button></Link>
                        </p>
                      </div>
                    </div>
    </div>
    </>
  );
}
