// A mock function to mimic making an async request for data
import { configUrl } from "../../app/contants";
export function fetchAllProducts() {
  return new Promise(async (resolve) => {
    const response = await fetch(`${configUrl.url}/products`);
    const data = await response.json();
    resolve({ data });
  });
}

export function fetchProductsByFilters(filter, sort,pagination,admin) {
  let queryString = "";
  // filter ={"category":{"smartPhone","laptops"]}}
  for (let key in filter) {
    const category = filter[key];
    if (category.length) {
      const lastCategory = category[category.length - 1];
      queryString += `${key}=${lastCategory}&`;
    }
  }
  // sort={_sort:"price",}
  for (let keySort in sort) {
    queryString += `${keySort}=${sort[keySort]}&`;
  }

 // pagination = {"_page":1,"limit":10}
 for (let keyPage in pagination) {
  queryString += `${keyPage}=${pagination[keyPage]}&`;
}
  return new Promise(async (resolve) => {
    const response = await fetch(
      `${configUrl.url}/products?${queryString}`
    );
    const data = await response.json();
    const totalItems=await response.headers.get('X-Total-Count')
    resolve({ data :{totalItems:totalItems,products:data}});
  });
}

