export function fetchAllProductById(id) {
  return new Promise(async (resolve) => {
    const response = await fetch(`${configUrl.url}/products/${id}`);
    const data = await response.json();
    resolve({ data });
  });
}

export function createNewProduct(product) {
  return new Promise(async (resolve) => {
    const response = await fetch(`${configUrl.url}/products/`,{
      method:"POST",
      body:JSON.stringify(product),
      headers:{'content-type':"application/json"}
    });
    const data = await response.json();
    resolve({ data });
  });
}

export function updateProduct(update) {
  return new Promise(async (resolve) => {
    const response = await fetch(`${configUrl.url}/products/${update.id}`,{
      method:"PATCH",
      body:JSON.stringify(update),
      headers:{'content-type':"application/json"}
    });
    const data = await response.json();
    resolve({ data });
  });
}