import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { addToCartProduct,fetchAllItemByUserId ,updateCart,deleteItemInCart,resetCart} from './cartAPI';
import {notify} from "../common/notify";
const initialState = {
  cart: [],
  status: 'idle',
  value:0,
  error:null,
  cartLoaded:false,
};

export const addToCartProductAsync = createAsyncThunk(
  'cart/addToCart',
  async (item) => {
    const response = await addToCartProduct(item);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const fetchAllItemByUserIdAsync = createAsyncThunk(
  'cart/fetchAllItemByUserIdAsync',
  async (id) => {
    const response = await fetchAllItemByUserId(id);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const updateCartAsync = createAsyncThunk(
  'cart/updateCart',
  async (item) => {
    const response = await updateCart(item);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const deleteItemInCartAsync = createAsyncThunk(
  'cart/deleteItemInCart',
  async (item) => {
    const response = await deleteItemInCart(item);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const resetCartAsync = createAsyncThunk(
  'cart/resetCart',
  async (userId) => {
    const response = await resetCart(userId);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const cartSlice = createSlice({
  name: 'cart',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(addToCartProductAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(addToCartProductAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.error=null;
        state.cart.push(action.payload);
        notify.success(action.payload.product.title +" added to cart");
      })
      .addCase(addToCartProductAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error=action.error;
        notify.error(action.error.message);
      })
      .addCase(fetchAllItemByUserIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllItemByUserIdAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error = action.error;
        state.cartLoaded=true;
        // notify.error(action.error.message);
      })
      .addCase(fetchAllItemByUserIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.cart=action.payload;
        state.cartLoaded=true;
      })
      .addCase(updateCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index = state.cart.findIndex(item=>item.id === action.payload.id)
        state.cart[index]=action.payload;
      })
      .addCase(deleteItemInCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(deleteItemInCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index = state.cart.findIndex(item=>item.id === action.payload.id)
        state.cart.splice(index, 1);
        state.error=null;
        notify.success("Item deleted in Cart successfully");
        
      })
      .addCase(deleteItemInCartAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.error = action.error;
        notify.error(action.error.message);
      })
      .addCase(resetCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(resetCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.cart=[];
      })
  }
});

export const { increment } = cartSlice.actions;

export const selectAddToCartProduct = (state) => state.cart.cart;

export const selectCartStatus = (state) => state.cart.status;
export const selectCartLoaded = (state) => state.cart.cartLoaded;
export default cartSlice.reducer;
