import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchAllProducts,fetchProductsByFilters,fetchAllProductBrand,fetchAllProductCategory ,fetchAllProductById,createNewProduct,updateProduct} from './productListAPI';

const initialState = {
  products: [],
  status: 'idle',
  totalItems: 0,
  brand:[],
  category:[],
  selectedProduct: null,
};

export const fetchAllProductsAsync = createAsyncThunk(
  'product/fetchAllProducts',
  async () => {
    const response = await fetchAllProducts();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const updateProductAsync = createAsyncThunk(
  'product/updateProduct',
  async (update) => {
    const response = await updateProduct(update);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const createNewProductAsync = createAsyncThunk(
  'product/createNewProduct',
  async (product) => {
    const response = await createNewProduct(product);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const fetchAllProductsBrandAsync = createAsyncThunk(
  'product/fetchAllProductBrand',
  async () => {
    const response = await fetchAllProductBrand();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const fetchAllProductsCategoryAsync = createAsyncThunk(
  'product/fetchAllProductCategory',
  async () => {
    const response = await fetchAllProductCategory();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchProductsByFiltersAsync = createAsyncThunk(
  'product/fetchProductsByFilters',
  async ({filter,sort,pagination,admin}) => {
    const response = await fetchProductsByFilters(filter,sort,pagination,admin);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchProductsByIdAsync = createAsyncThunk(
  'product/fetchProductsById',
  async (id) => {
    const response = await fetchAllProductById(id);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const productListSlice = createSlice({
  name: 'product',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    clearSelectedProduct: (state) => {
      state.selectedProduct = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchAllProductsAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllProductsAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.products = action.payload;
      })
      .addCase(fetchProductsByFiltersAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchProductsByFiltersAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.products = action.payload.products;
        state.totalItems = action.payload.totalItems;
      })
      .addCase(fetchAllProductsBrandAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllProductsBrandAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.brand = action.payload;
      })
      .addCase(fetchAllProductsCategoryAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllProductsCategoryAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.category = action.payload;
      })
      .addCase(fetchProductsByIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchProductsByIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.selectedProduct = action.payload;
      })
      .addCase(createNewProductAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(createNewProductAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.products.push(action.payload);
      })
      .addCase(updateProductAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateProductAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index=state.products.findIndex(product => product.id === action.payload.id)
        state.products[index]=action.payload;
      })
  }
});

export const { clearSelectedProduct } = productListSlice.actions;

export const selectAllProducts = (state) => state.product.products;
export const selectAllProductsCategory = (state) => state.product.category;
export const selectAllProductsBrand = (state) => state.product.brand;
export const selectAllProductsById = (state) => state.product.selectedProduct;
export const selectTotalItems = (state) => state.product.totalItems;
export const selectProductStatus = (state) => state.product.status;
export default productListSlice.reducer;
