import { toast } from "react-toastify";
import { css } from "glamor";
export const notify =  {
    success:function(message){
    toast(message,{
        hideProgressBar:false,
        type:"success",
        bodyClassName:css({
            fontSize:"14px"
        }),
        position:"top-right",
        closeOnClick:true,
        pauseOnHover:true,
        theme:"colored"
    });
    },
    error:function(message){
        toast(message,{
            hideProgressBar:true,
            type:"error",
            bodyClassName:css({
                fontSize:"14px"
            }),
            position:"top-right",
            closeOnClick:true,
        pauseOnHover:true,
        theme:"colored"
        });
    },
    warn:function(message){
        toast(message,{
            hideProgressBar:true,
            type:"warning",
            bodyClassName:css({
                fontSize:"14px"
            }),
            position:"top-right",
            closeOnClick:true,
        pauseOnHover:true,
        theme:"colored"
        });
    },
    info:function(message){
        toast(message,{
            hideProgressBar:true,
            type:"info",
            bodyClassName:css({
                fontSize:"14px",
            }),
            position:"top-right",
            closeOnClick:true,
        pauseOnHover:true,
        theme:"colored"
        });
    }
}

