import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import {
  fetchLoggedInUserOrdersAsync,
  selectLoggedInUserOrder,
  selectUserStatus,
} from "../userSlice";
import { selectLoggedInUser } from "../../auth/authSlice";
import { selectUserInfo } from "../userSlice";
import { discountPrice } from "../../../app/contants";
import Loader from "../../common/Loader";

const UserOrders = () => {
  const dispatch = useDispatch();
  const user = useSelector(selectUserInfo);
  const orders = useSelector(selectLoggedInUserOrder);
  const status =useSelector(selectUserStatus);
  useEffect(() => {
    dispatch(fetchLoggedInUserOrdersAsync(user.id));
  }, [dispatch]);
  return (
    <>
    {status === 'loading' ? <Loader />:null}
      {orders && orders.length > 0 ?
        orders.map((order, index) => (
          <div
            key={index}
            className="mx-auto mt-12 max-w-7xl px-4 sm:px-6 lg:px-8 bg-white"
          >
            <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
              <h1 className="text-4xl my-5 font-bold tracking-tight text-gray-900">
                Order Number : #{order.id}
              </h1>
              <h3 className="text-xl my-5 font-bold tracking-tight text-red-800">
                Order Status : {order.status}
              </h3>
              <div className="flow-root">
                <ul role="list" className="-my-6 divide-y divide-gray-200">
                  {order.items.map((product) => (
                    <li key={product.id} className="flex py-6">
                      <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                        <img
                          src={product.product.thumbnail}
                          alt={product.product.title}
                          className="h-full w-full object-cover object-center"
                        />
                      </div>

                      <div className="ml-4 flex flex-1 flex-col">
                        <div>
                          <div className="flex justify-between text-base font-medium text-gray-900">
                            <h3>
                              <a href={product.product.href}>{product.product.title}</a>
                            </h3>
                            <p className="ml-4">${discountPrice(product.product)}</p>
                          </div>
                          <p className="mt-1 text-sm text-gray-500">
                            {product.product.brand}
                          </p>
                        </div>
                        <div className="flex flex-1 items-end justify-between text-sm">
                          <div className="text-gray-500">
                            <label
                              htmlFor="quantity"
                              className="inline mr-5 text-sm font-medium leading-6 text-gray-900"
                            >
                              Qty: {product.quantity}
                            </label>
                          </div>
                        </div>
                      </div>
                    </li>
                  ))}
                </ul>
              </div>
            </div>

            <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
            <h3 className="text-xl my-5 font-bold tracking-tight text-gray-800">
                Shipping Address :
              </h3>
            <div  className="flex justify-between gap-x-6 py-5">
          <div className="flex min-w-0 gap-x-4">
            {/* <img className="h-12 w-12 flex-none rounded-full bg-gray-50" src={address.imageUrl} alt="" /> */}
            <div className="min-w-0 flex-auto">
              <p className="text-sm font-semibold leading-6 text-gray-900">
              {order.selectedAddress['first-name'].toUpperCase()+" " + order.selectedAddress['last-name'].toUpperCase()}
</p>
              <p className="mt-1 truncate text-xs leading-5 text-gray-500">{order.selectedAddress.email}</p>
              <p className="mt-1 truncate text-xs leading-5 text-gray-500">{order.selectedAddress.phone}</p>
            </div>
          </div>
          <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
            <p className="text-sm leading-6 text-gray-900">{order.selectedAddress.street}</p>
            
              {/* <p className="mt-1 text-xs leading-5 text-gray-500">
                 {order.selectedAddress.street}
              </p> */}
              <p className="mt-1 text-xs leading-5 text-gray-500">
                 {order.selectedAddress.city}{"  ,"}{order.selectedAddress.state}
              </p>
              <p className="mt-1 text-xs leading-5 text-gray-500">
                 {order.selectedAddress.zipCode}
              </p>
          </div>
        </div>
            </div>

            <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
              <div className="flex justify-between text-base my-2 font-medium text-gray-900">
                <p>Subtotal</p>
                <p>$ {order.totalAmount}</p>
              </div>
              <div className="flex justify-between my-2 text-base font-medium text-gray-900">
                <p>Total item in Cart</p>
                <p>{order.totalItems} Items</p>
              </div>
              <div className="flex justify-between my-2 text-base font-medium text-gray-900">
                <p>Payment Method</p>
                <p>{order.paymentMethod.toUpperCase()}</p>
              </div>
              <div className="mt-6">
                <Link
                  to={"/"}
                  className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                >
                  back
                </Link>
              </div>
            </div>
          </div>
        )):<>
        <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
        <div className="text-center">
          <p className="text-base font-semibold text-indigo-600">Orders</p>
          <h1 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl"> Your Orders not found</h1>
          <p className="mt-6 text-base leading-7 text-gray-600">Sorry, we couldn’t find the orders you’re looking for.</p>
          <div className="mt-10 flex items-center justify-center gap-x-6">
            <Link
              to="/"
              className="rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              Go back home
            </Link>
            <Link to="/product-list" className="text-sm font-semibold text-gray-900">
              Product List <span aria-hidden="true">&rarr;</span>
            </Link>
          </div>
        </div>
      </main>
        </>}
    </>
  );
};

export default UserOrders;
