// A mock function to mimic making an async request for data
import { configUrl } from "../../app/contants";
export function createOrder(order) {
  return new Promise(async(resolve) =>{
    const response= await fetch(`${configUrl.url}/order`,{
      method:"POSt",
      body:JSON.stringify(order),
      headers:{'content-type':'application/json'}
    });
    const data = await response.json();
    resolve({data});
});
}

export function updateOrder(order) {
  return new Promise(async(resolve) =>{
    const response= await fetch(`${configUrl.url}/order/${order.id}`,{
      method:"PATCH",
      body:JSON.stringify(order),
      headers:{'content-type':'application/json'}
    });
    const data = await response.json();
    resolve({data});
});
}


export function fetchAllOrders(sort,pagination) {
  let queryString = "";

  // sort={_sort:"price",}
  for (let keySort in sort) {
    queryString += `${keySort}=${sort[keySort]}&`;
  } 
  for (let keyPage in pagination) {
    queryString += `${keyPage}=${pagination[keyPage]}&`;
  }
    return new Promise(async (resolve) => {
      const response = await fetch(
        `${configUrl.url}/order?${queryString}`
      );
      const data = await response.json();
      const totalOrders=await response.headers.get('X-Total-Count')
      resolve({ data :{totalOrders:totalOrders,orders:data}});
    });
}
