import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link, Navigate } from 'react-router-dom';
import {
  selectLoggedInUser,
  createUserAsync,
  selectAuthStatus
} from '../authSlice';
import { useForm } from "react-hook-form";
import Loader from '../../common/Loader';
import {EyeSlashIcon,EyeIcon} from "@heroicons/react/24/outline"
import { GlobeAltIcon } from '@heroicons/react/20/solid';

export default function Signup() {
  const [passwordView,setPasswordView]=useState(false)
  // const count = useSelector(selectCount);
  const dispatch = useDispatch();
  const { register, handleSubmit, reset,clearErrors, formState: { errors } } = useForm();
  const user= useSelector(selectLoggedInUser);
  const status=useSelector(selectAuthStatus);
  const handleCreateLoggedInUser=(data)=>{
    clearErrors();
    dispatch(createUserAsync({email: data.email,fullName :data.fullName,role:"user", password: data.password,addresss:[],orders:[],thumbnail:""}));
    reset();
  }
  const handleShowHidePassword =(e)=>{
    setPasswordView(!passwordView);
  }
  return (
    <>
    {user && <Navigate to={"/"} replace={true} />}
{status ==="loading" ? <Loader /> : null}
    <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
        <div className="flex flex-col justify-center items-center sm:mx-auto sm:w-full sm:max-w-sm">
          <GlobeAltIcon className='h-30 w-32 text-indigo-600'/>
          <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900">
            Create a your account
          </h2>
        </div>

        <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
          <form noValidate className="space-y-6" onSubmit={handleSubmit(handleCreateLoggedInUser)}>
          <div>
              <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                Full Name<span className="text-red-500">*</span>
              </label>
              <div className="mt-2">
                <input
                  id="fullName"
                  {...register("fullName", 
                    {  
                      required: "Full Name are required" ,
                    }
                  )}
                  type="text"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                {errors.fullName &&<p className='text-red-500'>{errors.fullName.message}</p>}
              </div>
            </div>
            <div>
              <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                Email address<span className="text-red-500">*</span>
              </label>
              <div className="mt-2">
                <input
                  id="email"
                  {...register("email", 
                    {  
                      required: "email are required" ,
                      pattern: {
                        value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/gi ,
                        message: "email is not valid",
                      },
                    }
                  )}
                  type="email"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                {errors.email &&<p className='text-red-500'>{errors.email.message}</p>}
              </div>
            </div>

            <div>
              <div className="flex items-center justify-between">
                <label htmlFor="password" className="block text-sm font-medium leading-6 text-gray-900">
                  Password<span className="text-red-500">*</span>
                </label>
                <div className="text-sm">
                  
                </div>
              </div>
              <div className="mt-2 relative">
                <input
                  id="password"
                  {...register("password", { required: "password are required" ,
                  pattern: {
                        value: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/gm,
                        message: `- at least 8 characters\n\n
                        - must contain at least 1 uppercase letter, 1 lowercase letter, and 1 number \n\n
                        - Can contain special characters`,
                      },
                  })}
                  type={passwordView ? "text":"password"}

                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                <div className="absolute inset-y-0 right-5 flex items-center">
                    <label htmlFor="password" className="sr-only">Show Password</label>
                     {passwordView ?<EyeSlashIcon className="h-6 w-6 text-indigo-600 cursor-pointer" onClick={handleShowHidePassword} /> :<EyeIcon className="h-6 w-6 text-indigo-600 cursor-pointer" onClick={handleShowHidePassword}/>}
                  </div>
                
              </div>
              {errors.password && <p className='text-red-500'>{errors.password.message}</p>}
            </div>
            <div>
              <div className="flex items-center justify-between">
                <label htmlFor="password" className="block text-sm font-medium leading-6 text-gray-900">
                  Confirm Password<span className="text-red-500">*</span>
                </label>
                <div className="text-sm">
                  
                </div>
              </div>
              <div className="mt-2">
                <input
                  id="confirm-password"
                  {...register("confirm-password", { required: "confirm password are required",
                  validate:(value,formValues)=> value ===formValues.password || 'password did not match'
                   })}
                  type={passwordView ? "text":"password"}
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                {errors['confirm-password'] &&<p className='text-red-500'>{errors['confirm-password'].message}</p>}
              </div>
            </div>
            <div>
              <button
                type="submit"
                className="flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Sign Up
              </button>
            </div>
          </form>

          <p className="mt-10 text-center text-sm text-gray-500">
            Already member?{' '}
            <Link to="/login" className="font-semibold leading-6 text-indigo-600 hover:text-indigo-500">
              Log In
            </Link>
          </p>
        </div>
      </div>
      </>
  );
}
