import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Link, Navigate } from 'react-router-dom';
import { useForm } from "react-hook-form";
import { selectAuthStatus,resetPasswordRequestAsync,selectMailSent } from '../authSlice';
import Loader from '../../common/Loader';
import { GlobeAltIcon } from '@heroicons/react/20/solid';
const ForgotPassword = () => {
    const { register, handleSubmit, reset,watch, formState: { errors } } = useForm();
    const status = useSelector(selectAuthStatus);
    const isMailSent = useSelector(selectMailSent);

    const dispatch=useDispatch();
  const handleForgotPassword =(data)=>{
    dispatch(resetPasswordRequestAsync(data));
     reset();
  }
  return (
    <>
    {status ==="loading"? <Loader />: null}
      <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
        <div className="flex flex-col justify-center items-center sm:mx-auto sm:w-full sm:max-w-sm">
        <GlobeAltIcon className='h-30 w-32 text-indigo-600'/>
          <h2 className="mt-10 text-center text-2xl font-bold leading-9 tracking-tight text-gray-900">
            Forgot password for your account
          </h2>
        </div>

        <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
          <form noValidate className="space-y-6" onSubmit={handleSubmit(handleForgotPassword)} >
            <div>
              <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                Email address<span className="text-red-500">*</span>
              </label>
              <div className="mt-2">
                <input
                  id="email"
                  {...register("email", { required: "email are required" },{ pattern: {value:/\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/gi ,message:"email is not valid"}})}
                  type="email"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
                {errors.email &&<p className='text-red-500'>{errors.email.message}</p>}
              </div>
              {isMailSent && <p className='text-green-600'>Mail send on your email.</p>}
            </div>
        <div>
              <button
                type="submit"
                className="flex w-full justify-center rounded-md bg-indigo-600 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Recovery Password
              </button>
            </div>
          </form>

          <p className="mt-10 text-center text-sm text-gray-500">
            Send me back to {' '}
            <Link to="/login" className="font-semibold leading-6 text-indigo-600 hover:text-indigo-500">
              Login
            </Link>
          </p>
        </div>
      </div>
    </>
  )
}

export default ForgotPassword;
