import React,{useState,useEffect} from 'react'
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import StripeCheckoutFrom from "./StripeCheckoutFrom";
import { Link ,Navigate} from "react-router-dom";
import { notify } from "../common/notify";
import { useSelector, useDispatch } from 'react-redux';
import {selectCurrentOrder } from "../order/orderSlice";
// import { selectAddToCartProduct} from '../cart/cartSlice';
import { selectUserInfo } from "../user/userSlice";
import { discountPrice } from "../../app/contants";
import "./StripeCss.css";
import { configUrl } from '../../app/contants';
import Loader from '../common/Loader';
const stripePromise = loadStripe("pk_test_51OMupNSD3SBgteJyTJIHbSKgnwjp7vyuSTPitDuTAOlkdrPYToYqFXwSGbGbzIBWoZQn4QJCi4y3q32VtNOknXpf00CMzjosQ5");

const StripeCheckout = () => {
    const [clientSecret, setClientSecret] = useState("");
    const dispatch = useDispatch();
    const user =useSelector(selectUserInfo);
    const currentOrder = useSelector(selectCurrentOrder);
    // const items=useSelector(selectAddToCartProduct);
    // const totalAmount= items.reduce((amount, item) => discountPrice(item.product)* item.quantity+amount,0);
    // const totalItems= items.reduce((amount, item) => item.quantity+amount,0);
  useEffect(() => {
    console.log(currentOrder)
   // Create PaymentIntent as soon as the page loads
   if(!currentOrder){
    return true
   }
    fetch(`${configUrl.url}/payment/stripe-create-payment-intent`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({currentOrder}),
    })
      .then((res) => res.json())
      .then((data) => setClientSecret(data.clientSecret));
  }, [currentOrder]);
  const appearance = {
    theme: 'stripe',
  };
  const options = {
    clientSecret,
    appearance,
  };

  return (
    <div className='Stripe'>
      {!clientSecret ?<>
        <Loader />
      </> :(
        <Elements options={options} stripe={stripePromise}>
          <StripeCheckoutFrom />
        </Elements>
      )}
    </div>
  )
}

export default StripeCheckout;
